.movies {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    top: 90px
}

.movieContainer {
    position: relative;
    overflow: hidden;
    height: auto;
    margin: 30px;
    height: 450px;
    max-width: 300px;
    border-radius: 10px;
    -webkit-box-shadow: 11px 10px 25px 0px rgba(0, 0, 0, 0.54);
    -moz-box-shadow: 11px 10px 25px 0px rgba(0, 0, 0, 0.54);
    box-shadow: 11px 10px 25px 0px rgba(0, 0, 0, 0.54);
    transition: all .2s ease-in-out;
}

.movieContainer:hover {
    transform: scale(1.1);
}

.poster {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: auto;
}

.title {
    position: absolute;
    bottom: 20px;
    opacity: 0;
    text-align: center;
    width: 100%;
    pointer-events: none;
    color: white;
    font-size: 1.4em;
}

.movieContainer:hover p {
    opacity: 1;
}

.gradient {
    opacity: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(0, 0, 0);
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.95) 0%, rgba(255, 255, 255, 0) 80%);
}

.gradient:hover {
    opacity: 1;
}