.languagePreviewText {
    color: white;
    padding-right: 20px;
}

.languagePreviewText:hover {
    cursor: default;
}

.languagePreviewContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.dropdownElement {
    width: auto;
    color: white;
    padding: 15px 10px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}

.dropdownElement:last-child {
    border-bottom: none;
}

.menu ul {
    transition: all .5s ease-in-out;
    background: rgba(0, 0, 0, 1);
    height: 0;
    opacity: 0;
    position: absolute;
    left: -5px;
    transition: all .5s ease;
    border-radius: 7px;
}

.menu li:hover ul {
    height: auto;
    opacity: 1;
    transform: translateY(0);
}

.menu li:hover ul:hover {
    cursor: pointer;
}

ul {
    list-style-type: none;
}

.menu {
    position: relative;
    z-index: 2;
}