.ul {
    width: 100%;
    display: flex;
    justify-content: space-around;
    height: 80px;
    align-items: center;
    background-color: black;
    position: fixed;
    z-index: 2;
}

.topBarElement {
    list-style: none;
    color: white;
    font-size: 1.5em;
    font-weight: 300;
    text-decoration: none;
    transition: all .2s ease-in-out;
}

.li:hover {
    border-bottom: 1px solid white;
    transform: scale(1.1);
}